@import '../../../common/styles/variables.scss';

:host {
  display: block;
  width: 400px;
  @include default-css-variables;
}

sp-theme {
  height: inherit;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  font-family: var(--nylas-font-family);
}

.nylas-cancel-booking-form {
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: var(--nylas-base-0);
  color: var(--nylas-base-800);
  border-radius: var(--nylas-border-radius-2x);
  padding: 1.5rem;
  position: relative;
  box-shadow:
    0px 1px 4px rgba(0, 0, 0, 0.1),
    0px 3px 6px rgba(0, 0, 0, 0.06);
}

.nylas-cancel-booking-form__title {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 0;
  color: var(--nylas-base-900);
}

.nylas-cancel-booking-form__description {
  font-size: 1rem;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  color: var(--nylas-base-600);
}

.nylas-cancel-booking-form__calendar-icon {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  position: absolute;
  top: -1.25rem;
  left: 50%;
  transform: translateX(-50%);
  background-color: var(--nylas-base-0);
  border: 1px solid var(--nylas-base-200);
}

calendar-cancel-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

sp-button {
  background-color: var(--nylas-base-0);
  color: var(--nylas-base-700);
  border: 1px solid var(--nylas-base-200);
  border-radius: var(--nylas-border-radius-2x);
  font-size: 16px;
  font-weight: 600;
  width: 100%;
  height: 48px;
  padding: 8px;
  line-height: 24px;
  font-family: var(--nylas-font-family);
  &.cancel {
    margin-top: 1.25rem;
    background-color: var(--nylas-primary);
    color: var(--nylas-base-0);
    &:hover,
    &:focus {
      background-color: var(--nylas-base-600);
    }
    &:active {
      background-color: var(--nylas-base-800);
    }
  }
  &.back {
    margin-top: 0.5rem;
    &:hover,
    &:focus {
      border-color: var(--nylas-primary);
    }
    &:active {
      border-color: var(--nylas-base-600);
    }
  }
}

sp-field-label {
  margin-top: 1rem;
  display: flex;
  color: var(--nylas-base-800);
  font-size: 14px;
  &.error {
    color: var(--nylas-error);
  }
  span.required {
    color: var(--nylas-error);
  }
}
sp-textfield {
  --spectrum-textfield-border-color: var(--nylas-base-300);
  --spectrum-textfield-icon-color-invalid: var(--nylas-error);
  position: relative;
  width: 100%;
  height: 48px;
  margin-bottom: 16px;
  background-color: var(--nylas-base-0);
  color: var(--nylas-base-500);
  &.error {
    --spectrum-textfield-border-color: var(--nylas-error);
  }
}

sp-help-text {
  margin: 0.25rem 0 1rem 0;
  color: var(--nylas-error);
}
