import { RegisterComponent } from '@/common/register-component';
import { Component, Event, EventEmitter, h, Host, Prop, State } from '@stencil/core';
import { NylasEvent, NylasSchedulerConnector, NylasSchedulerErrorResponse } from '../../..';
import { NylasScheduling } from '../nylas-scheduler/nylas-scheduling';
import { debug } from '@/utils/utils';
import { ConfigSettings } from '@/stores/scheduler-store';

/**
 * The `nylas-cancelled-event-card` component is a UI component that displays the cancelled event card.
 *
 * @part ncec - The cancelled event card host.
 * @part ncec__icon - The calendar icon.
 * @part ncec__title - The title of the cancelled event card.
 * @part ncec__description - The description of the cancelled event card.
 * @part ncec__button-outline - The close button CTA.
 * @part ncec__card - The cancelled event card.
 */
@Component({
  tag: 'nylas-cancelled-event-card',
  styleUrl: 'nylas-cancelled-event-card.scss',
  shadow: true,
})
export class NylasCancelledEventCard {
  /**
   * The participant's name who booked the event / is logged in.
   */
  @Prop() readonly cancelledEventInfo!: Partial<NylasEvent>;

  /**
   * Closing state of the cancelled event card button.
   */
  @State() isClosing: boolean = false;

  /**
   * The config settings for the scheduler.
   */
  @Prop() readonly configSettings?: ConfigSettings;

  /**
   * This event is fired when the close button is clicked on the cancelled event card.
   */
  @Event() readonly closeCancelEventCardClicked!: EventEmitter<{ errorHandler?: (error: NylasSchedulerErrorResponse) => void }>;

  /**
   * This event is fired when an error occurs.
   */
  @Event() readonly cancelledEventCardError!: EventEmitter<NylasSchedulerErrorResponse>;

  connectedCallback() {
    debug(`[nylas-cancelled-event-card] Component connected`);
  }

  async componentWillLoad() {
    debug(`[nylas-cancelled-event-card] Component will load`);
  }

  async componentDidLoad() {
    debug(`[nylas-cancelled-event-card] Component did load`);
  }

  disconnectedCallback() {
    debug(`[nylas-cancelled-event-card] Component disconnected`);
  }

  private handleCloseClicked = () => {
    this.isClosing = true;
    const errorHandler = (error: NylasSchedulerErrorResponse) => {
      this.cancelledEventCardError.emit(error);
      this.isClosing = false;
    };
    this.closeCancelEventCardClicked.emit({ errorHandler });
  };

  @RegisterComponent<NylasCancelledEventCard, NylasSchedulerConnector, Exclude<NylasScheduling['stores'], undefined>>({
    name: 'nylas-cancelled-event-card',
    stateToProps: new Map([['scheduler.configSettings', 'configSettings']]),
    eventToProps: {
      closeCancelEventCardClicked: async (_: CustomEvent<{ errorHandler?: (error: NylasSchedulerErrorResponse) => void }>, nylasschedulerconnector: NylasSchedulerConnector) => {
        const result = await nylasschedulerconnector.scheduler.resetCancel();

        const { errorHandler } = _.detail;
        if (errorHandler && (!result || 'error' in result)) {
          errorHandler(result);
        }
      },
    },
    fireRegisterEvent: true,
  })
  render() {
    return (
      <Host part="ncec">
        <sp-theme theme="spectrum" color="dark" scale="medium">
          <div class="nylas-cancelled-event-card" part="ncec__card">
            <div class="nylas-cancelled-event-card__calendar-icon" part="ncec__icon">
              <calendar-cancel-icon />
            </div>
            <h3 class="nylas-cancelled-event-card__title" part="ncec__title">
              Your booking has been cancelled successfully!
            </h3>
            <div class="nylas-cancelled-event-card__description" part="ncec__description">
              A cancellation email has been sent to {this.configSettings?.organizer?.name || 'the participants'}.
            </div>
            <div class="nylas-cancelled-event-card__cta">
              <sp-button variant="secondary" treatment="outline" disabled={this.isClosing} part="ncec__button-outline" onClick={this.handleCloseClicked}>
                {this.isClosing ? 'Closing...' : 'Close'}
              </sp-button>
            </div>
          </div>
        </sp-theme>
      </Host>
    );
  }
}
