import { RegisterComponent } from '@/common/register-component';
import { Component, Event, EventEmitter, h, Host, Prop, State } from '@stencil/core';
import { NylasSchedulerConnector } from '../../..';
import { NylasScheduling } from '../nylas-scheduler/nylas-scheduling';
import { debug } from '@/utils/utils';
import { Notification, NylasSchedulerErrorResponse } from '@nylas/core';

/**
 * The `nylas-cancel-booking-form` component is a UI component that allows users to cancel a booking.
 */
@Component({
  tag: 'nylas-cancel-booking-form',
  styleUrl: 'nylas-cancel-booking-form.scss',
  shadow: true,
})
export class NylasCancelBookingForm {
  /**
   * The booking ID to cancel.
   */
  @Prop() readonly cancelBookingId!: string;

  /**
   * This event is fired when the Go back button is clicked on the cancel booking form.
   */
  @Event() readonly goBackButtonClicked!: EventEmitter<void>;

  /**
   * This event is fired when the cancel booking form is submitted.
   */
  @Event() readonly cancelBookingFormSubmitted!: EventEmitter<{ bookingId: string; reason: string; errorHandler?: (error: NylasSchedulerErrorResponse) => void }>;

  /**
   * This event is fired when an error occurs while cancelling the booking.
   */
  @Event() readonly cancelBookedEventError!: EventEmitter<NylasSchedulerErrorResponse>;

  /**
   * This event is fired when an error occurs in the booking form.
   */
  @Event() cancelBookingFormError!: EventEmitter<Partial<Notification>>;

  /**
   * The reason for cancellation.
   */
  @State() cancellationReason: string = '';

  @State() cancellationError: string = '';

  connectedCallback() {
    debug(`[nylas-cancel-booking-form] Component connected`);
  }

  async componentWillLoad() {
    debug(`[nylas-cancel-booking-form] Component will load`);
  }

  async componentDidLoad() {
    debug(`[nylas-cancel-booking-form] Component did load`);
  }

  disconnectedCallback() {
    debug(`[nylas-cancel-booking-form] Component disconnected`);
  }

  private handleGoBackClicked = () => {
    this.goBackButtonClicked.emit();
  };

  private handleSubmitCancelBooking = (event: Event) => {
    event.preventDefault();
    if (!this.cancellationReason) {
      this.cancellationError = 'Please provide a reason for cancellation.';
      this.cancelBookingFormError.emit({ title: 'Cancel booking form error', description: 'Please provide a reason for cancellation.' });
      return;
    }

    debug(`[nylas-cancel-booking-form] Cancel booking form submitted with reason: ${this.cancellationReason}`);
    const errorHandler = (error: NylasSchedulerErrorResponse) => {
      this.cancelBookedEventError.emit(error);
    };
    this.cancelBookingFormSubmitted.emit({ bookingId: this.cancelBookingId, reason: this.cancellationReason, errorHandler });
  };

  private handleOnChangeCancellationReason = (event: Event) => {
    this.cancellationError = '';
    this.cancellationReason = (event.target as HTMLInputElement).value;
  };

  @RegisterComponent<NylasCancelBookingForm, NylasSchedulerConnector, Exclude<NylasScheduling['stores'], undefined>>({
    name: 'nylas-cancel-booking-form',
    eventToProps: {
      cancelBookingFormSubmitted: async (
        event: CustomEvent<{ bookingId: string; reason: string; errorHandler?: (error: NylasSchedulerErrorResponse) => void }>,
        nylasSchedulerConnector: NylasSchedulerConnector,
      ) => {
        const result = await nylasSchedulerConnector.scheduler.cancelBooking(event.detail.bookingId);
        const { errorHandler } = event.detail;
        if (errorHandler && (!result || 'error' in result)) {
          errorHandler(result);
        }
      },
    },
    fireRegisterEvent: true,
  })
  render() {
    return (
      <Host part="ncec">
        <sp-theme theme="spectrum" scale="medium">
          <div class="nylas-cancel-booking-form" part="ncec__card">
            <div class="nylas-cancel-booking-form__calendar-icon" part="ncec__icon">
              <calendar-cancel-icon />
            </div>
            <h3 class="nylas-cancel-booking-form__title" part="ncec__title">
              Cancel booking?
            </h3>
            <div class="nylas-cancel-booking-form__description" part="ncec__description">
              Your current timeslot will become available to others.
            </div>
            <form onSubmit={this.handleSubmitCancelBooking}>
              <sp-field-label for="cancel-reason" class={this.cancellationError ? 'error' : ''}>
                Reason for cancellation <span class="required">*</span>
              </sp-field-label>
              <sp-textfield
                id="cancel-reason"
                class={this.cancellationError ? 'error' : ''}
                part="ncec__reason-textarea"
                multiline
                value={this.cancellationReason}
                onInput={this.handleOnChangeCancellationReason}
                onFocus={this.handleOnChangeCancellationReason}
                invalid={!!this.cancellationError}
              >
                <sp-help-text slot="negative-help-text">{this.cancellationError}</sp-help-text>
              </sp-textfield>
              <sp-button variant="primary" class="cancel" type="submit" part="ncec__button-cta">
                Cancel booking
              </sp-button>
              <sp-button variant="secondary" class="back" treatment="outline" part="ncec__button-outline" onClick={this.handleGoBackClicked}>
                Go back
              </sp-button>
            </form>
          </div>
        </sp-theme>
      </Host>
    );
  }
}
